.activity {
  margin: 192px 0 212px;
}

.activity__title {
  letter-spacing: 0.027em;
  color: #316099;
  margin-bottom: 158px;
  max-width: 360px;
}

.activity__inner {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
}

.activity__inner-left {
  display: flex;
  flex: 1 0 auto;
  flex-flow: column;
  margin-right: 100px;
}

.activity__inner-left .item {
  align-self: flex-end;
}

.activity__inner-right {
  display: flex;
  flex: 0 0 auto;
  flex-flow: column;
}

.activity__inner-right .item:last-of-type {
  align-self: flex-end;
  margin: 0;
}

@media only screen and (max-width: 991px) {
  .activity__inner {
    flex-flow: column nowrap;
  }

  .activity__inner-left {
    margin-right: 0;
  }

  .activity__inner-right {
    flex: 1 0 auto;
  }
}